import { useHttp } from '../hooks/http.hook'

const useMarvelService = () => {

    const {loading, request, error, clearError} = useHttp();

    const _apiBase = 'https://gateway.marvel.com:443/v1/public/';
    const _apiKey = 'apikey=d04fa5daa548e140a4c68e1b1b1a47f2';
    const _baseOffset = 0;

    //Метод по получение данных нескольких персонажей (в данном случае 9 limit=9 и отступом 250 персонажей от начала offset=250).Метод должен дождаться данных с севрера поэтому используем async и await
    const getAllCharacters = async (offset = _baseOffset) => {
        const res = await request(`${_apiBase}characters?limit=9&offset=${offset}&${_apiKey}`);
        return res.data.results.map(_transformCharacter);//Перебираем каждые элемент массива и преобразуем в удобный нам формат при помощи метода _transformeCharacter
    }

    // Вариант модификации готового метода для поиска по имени. 
    // Вызывать его можно вот так: getAllCharacters(null, name)

    // const getAllCharacters = async (offset = _baseOffset, name = '') => {
    //     const res = await request(`${_apiBase}characters?limit=9&offset=${offset}${name ? `&name=${name}` : '' }&${_apiKey}`);
    //     return res.data.results.map(_transformCharacter);
    // }

    // Или можно создать отдельный метод для поиска по имени

    const getCharacterByName = async (name) => {
        const res = await request(`${_apiBase}characters?name=${name}&${_apiKey}`);
        return res.data.results.map(_transformCharacter);
    }

    //Метод по получению одного конкретного персонажа. Метод должен дождаться данных с севрера поэтому используем async и await
    const getCharacter = async (id) => {
        const res = await request(`${_apiBase}characters/${id}?${_apiKey}`);
        return _transformCharacter(res.data.results[0]);//res.data.results[0] - объект персонажа 
    }

    //Метод получения данных нескольких комиксов
    const getAllComics = async (offset = _baseOffset) => {
        const res = await request(`${_apiBase}comics?limit=8&offset=${offset}&${_apiKey}`);
        return res.data.results.map(__transformeComics);//Перебираем каждый элемент массива и преобразуем в удобный нам формат при помощи метода _transformeComics
    }

    //Метод получения данных одного комикса
    const getComic = async (id) => {
        const res = await request(`${_apiBase}comics/${id}?${_apiKey}`);
        return __transformeComics(res.data.results[0])
    }




    //Метод трансформирующий данные по персонажу в удобный нам формат
    const _transformCharacter = (char) => {
        if (char.description === '') {
            char.description = 'Описание отсутствует'
        } else if (char.description.length > 210) {
            char.description = char.description.substr(0, 210) + '...'
        }
        return {
            id: char.id,
            name: char.name,
            description: char.description,
            thumbnail: char.thumbnail.path + '.' + char.thumbnail.extension,
            homepage: char.urls[0].url,
            wiki: char.urls[1].url,
            comics: char.comics.items
        }
    } 

    //Метод трансформирующий данные по комиксам в удобный формат
    const __transformeComics = (comics) => {
        return {
            id: comics.id,
            title: comics.title,
            description: comics.description || 'Описание отсутствует',
            pageCount: comics.pageCount ? `${comics.pageCount} pages` : 'Кол-во стр. не известно',
            language: comics.textObjects.language,
            prices: comics.prices[0].price ? `${comics.prices[0].price}$` : 'Цена неизвестна',
            thumbnail: comics.thumbnail.path + '.' + comics.thumbnail.extension,
        }
    } 

    return {loading, error, getAllCharacters, getCharacter, clearError, getAllComics, getComic, getCharacterByName}
}

export default useMarvelService;