import { useState, useCallback } from 'react'

export const useHttp = () => {
    const [loading, setLoading] = useState(false);//Изначально статус загрузки false
    const [error, setError] = useState(null);

    const request = useCallback(async(url, method = 'GET', body = null, headers = {'Content-Type': 'aplication/json'}) => {

        setLoading(true);//Как запустили функцию request, значит идет загрузка и устанавливаем loading в true

        try {
            const response = await fetch(url, {method, body, headers});

            if(!response.ok) {
                throw new Error(`Could not fetch ${url}, status ${response.status}`);
            }

            const data = await response.json();

            setLoading(false);//после того как данные загрузились устанавливаем loading обратно в false

            return data;
            
        } catch (e) {
            setLoading(false);//если произошла ошибка, то присваимваем loading в false
            setError(e.message);//если произошла ошибка, то присваиваем error сообщение ошибки
            throw(e);
        }

    }, []);

    const clearError = useCallback(() => setError(null), []);

    return {loading, request, error, clearError}
}